.theme-switch-wrapper {
    display: flex;
    align-items: center;
    position: fixed;
    top: 24px;
    right: 24px;

    em {
        margin-left: 10px;
        font-size: 12px;
        letter-spacing: 0.02em;
        font-family: "Andalé Mono", monospace;
        text-transform: uppercase;
        font-style: normal;
        color: var(--text-primary);
    }
}

.theme-switch,
.switch {
    display: inline-block;
    height: 24px;
    position: relative;
    width: 48px;
}

.theme-switch input,
.switch input {
    display: none;
}

.slider {
    background-color: var(--bg-secondary);
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    bottom: 0px;
    top: 0px;
    transition: 0.4s;
    outline: 1px solid var(--input-stroke);
}

.slider:hover {
    outline: 1px solid var(--input-stroke-hover);
}

.slider:before {
    background-color: var(--bg-primary-inverse);
    bottom: 2px;
    content: "";
    height: 20px;
    left: 2px;
    position: absolute;
    transition: 0.4s;
    width: 20px;
}

input:checked+.slider {
    background-color: $atmos5;
    outline: none;
}

input:checked+.slider:before {
    background-color: $grey1;
    transform: translateX(24px);
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    background-color: var(--bg-primary-inverse);
    border-radius: 50%;
}